:root {
  --fontColorOne: #212121;
  --fontFamilyOne: "Source Sans Pro";
  --fontFamilyTwo: "Kanit";
  --backgroundOne: " #F5F5F5";
}

.detailFields {
  padding: 40px 60px;
  font-family: var(--fontFamilyOne);
  //   height: 60vh;
  //   overflow: scroll;
  .basicInfo {
    h4 {
      font-size: 1.45rem;
      font-weight: 600;
    }
  }
}

.apiBox {
  width: 100%;
  height: 400px;
  background-color: #fff;
  box-shadow: 8px 15px 30px #0000001a;
  border-radius: 7px;
  .header {
    display: flex;
    justify-content: space-around;
    height: 65px;
    background-color: #e5e5e5;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    border-bottom: 1px solid #741763;
    .menuWrapper {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      h5 {
        margin-bottom: 0;
        font-family: var(--fontFamilyOne);
        font-weight: 600;
        color: #706767;
      }
    }
    .activeLeftMenu {
      background-color: #fff;
      border-top-left-radius: 7px;
      h5 {
        color: #741763;
      }
    }
    .activeMidMenu {
      background-color: #fff;
      h5 {
        color: #741763;
      }
    }
    .activeRightMenu {
      background-color: #fff;
      border-top-right-radius: 7px;
      h5 {
        color: #741763;
      }
    }
  }
  .body {
    padding: 20px 40px;
  }
}

.tableCard {
  h3 {
    text-align: center;
    font-family: var(--fontFamilyOne);
    font-size: 1.2rem;
    font-weight: 600;
    margin-top: 20px;
    margin-bottom: 30px;
  }
  thead {
    background-color: #741763;
    color: #fff;
    font-family: var(--fontFamilyOne);
    font-size: 1.05rem;
  }
  tbody {
    font-family: var(--fontFamilyOne);
    font-size: 1.1rem;
  }
}

.status {
  width: 200px;
  margin: 0 auto;
  background: #ffffff;
  box-shadow: 10px 10px 25px rgba(0, 0, 0, 0.1);
  padding: 9px 10px;
  border-radius: 9px;
  margin-bottom: 40px !important;
  p {
    text-align: center;
    margin-bottom: 0;
    font-size: 1.05rem;
  }
}

.monoContainer {
  width: 300px;
  margin: 0 auto;
}

.disburseModal {
  padding: 30px 35px;
}

.limitError {
  color: red;
  margin-bottom: 0;
}

.repaymentBtn {
  border-radius: 0 4px 4px 0;
  width: 100%;
  padding: 8px 16px;
  background-color: #741763;
  color: #fff;
  border: 1px solid #741763;
  margin-left: -2px;
  &:disabled,
  &[disabled] {
    opacity: 0.7;
    cursor: not-allowed;
  }
  &:focus {
    outline: none;
  }
}
