:root {
  --fontColorOne: #212121;
  --fontFamilyOne: "Source Sans Pro";
  --fontFamilyTwo: "Kanit";
  --backgroundOne: " #F5F5F5";
}

.formWrapper {
  width: 100%;
  height: 65vh;
  overflow: scroll;
  .formHeader {
    padding: 25px 40px;
    height: 180px;
    background-color: #741763;
    text-align: center;
    img {
      width: 140px;
      margin: 0 auto;
    }
    hr {
      border: 1px solid #fff;
    }
    .approvedDate {
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: #fff;
      font-size: 1.1rem;
      font-family: var(--fontFamilyOne);
      p {
        margin-bottom: 0;
        font-weight: 600;
      }
      .dateGroup {
        p {
          margin-bottom: 0;
          margin-right: 10px;
          font-weight: 600;
        }
        input {
          padding: 5px 20px;
          border-radius: 2px;
          border: 1px solid #fff;
          width: 170px;
          &.error {
            border: 0.5px solid red;
          }
          &:active {
            outline: none;
          }
          &:focus {
            outline: none;
          }
        }
        display: flex;
        align-items: center;
      }
    }
  }
  .formBody {
    background-color: #e5e5e5;
    padding: 30px 40px;
    font-family: var(--fontFamilyOne);
    font-size: 1.1rem;
    .addressGroup {
      .greet {
        p {
          margin-bottom: 0;
          margin-right: 10px;
        }
        display: flex;
        margin-bottom: 18px;
        align-items: center;
        input {
          padding: 5px 20px;
          border-radius: 2px;
          border: 0.5px solid #741763;
          width: 220px;
          &:active {
            outline: none;
          }
          &:focus {
            outline: none;
          }
        }
      }
      input {
        padding: 5px 20px;
        border-radius: 2px;
        border: 0.5px solid #741763;
        width: 350px;
        &.error {
          border: 0.5px solid red;
        }
        &:active {
          outline: none;
        }
        &:focus {
          outline: none;
        }
      }
    }
    .mainContent {
      margin-top: 60px;
      .letterHead {
        text-align: center;
        font-family: var(--fontFamilyOne);
        font-size: 1.4rem;
        font-weight: 600;
        display: block;
      }
      p {
        display: inline;
        font-size: 1.15rem;
        line-height: 1.8rem;
      }
      input {
        display: inline;
        padding: 5px 20px;
        border-radius: 2px;
        border: 0.5px solid #741763;
        width: 350px;
        &.error {
          border: 0.5px solid red;
        }
        &:active {
          outline: none;
        }
        &:focus {
          outline: none;
        }
        margin-top: 5px;
      }
      .loanData {
        margin-top: 35px;
        p {
          font-weight: 600;
          margin-bottom: 0;
        }
      }
      .terms {
        .title {
          font-weight: 600;
          font-size: 1.5rem;
          margin-top: 60px;
          margin-bottom: 25px;
          display: block;
        }
        p {
          display: block;
        }
        ol {
          margin-top: 15px;
          li {
            margin-bottom: 15px;
          }
        }
      }
      hr {
        border: 1px solid #000;
        margin-top: 60px;
      }
      .importantNotice {
        font-weight: 700;
        margin-top: 35px;
        p {
          display: block;
        }
      }
      .signatureGroup {
        margin-top: 40px;
        display: flex;
        justify-content: space-between;
        .signatureWrapper {
          margin-top: 10px;
          width: 150px;
          height: 90px;
          background-color: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            width: 120px;
          }
        }
        .signatureWrapper2 {
          margin-top: 10px;
          width: 280px;
          height: 90px;
          background-color: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            width: 120px;
          }
        }
        p {
          margin-bottom: 10px;
          font-weight: 600;
        }
        div {
          display: flex;
          flex-direction: column;
        }
      }
      .memorandum {
        margin-top: 50px;
        .title {
          font-weight: 600;
          font-size: 1.5rem;
          margin-top: 60px;
          margin-bottom: 25px;
          display: block;
        }
        .clientSign {
          margin-top: 40px;
          margin-bottom: 40px;
          input {
            width: 200px;
          }
        }
      }
    }
  }
}

.offerBtn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 45px;
}

.downloadBtn {
  display: block;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1.15rem;
  font-weight: 700;
  border-radius: 4px;
  border: none;
  padding: 12px 28px;
  color: #fff;
  background-color: rgba(116, 23, 99, 1);
  &:focus {
    outline: none;
  }
  &:hover {
    background-color: rgba(116, 23, 99, 0.9);
    text-decoration: none;
    color: #fff;
  }
  &:disabled,
  &[disabled] {
    opacity: 0.7;
  }
}
