.heroSection {
  padding: 0px 10px 0;
  min-height: 100vh;
  background-color: #f5f5f5;
  margin-top: -100px;
  display: flex;
  align-items: center;
  justify-content: center;
  .container {
    margin: 0 auto;
    margin-top: 90px;
    max-width: 1200px;
  }
  .mainCopy {
    .copyGroup {
      position: relative;
      .pattern {
        position: absolute;
        top: -10%;
        left: 2%;
        width: 100px;
        z-index: 1;
      }
    }
    display: flex;
    align-items: center;
    justify-content: center;
    h1 {
      font-size: 2.9rem;
      font-weight: 700;
      width: 100%;
      text-align: center;
      color: #a02089;
      z-index: 3;
      position: relative;
    }
    h3 {
      width: 100%;
      font-size: 1.5rem;
      margin-top: 20px;
      text-align: center;
      font-weight: 600;
      font-family: "Kanit", sans-serif;
      color: #2f2f2f;
    }
    input {
      border-radius: 35px;
      padding: 10px 10px;
      width: 90%;
      margin: 0 auto;
      text-align: center;
      border: 1px solid #a02089;
      margin-top: 35px;
      font-family: "Source Sans Pro", sans-serif;
      display: block;
      font-size: 1.15rem;
      &:focus {
        outline: none;
      }
    }
    button {
      font-family: "Source Sans Pro", sans-serif;
      width: 90%;
      display: block;
      border-radius: 35px;
      margin: 0 auto;
      padding: 10px 10px;
      background-color: #a02089;
      color: #fff;
      border: 1px solid #a02089;
      margin-top: 15px;
      font-size: 1.15rem;
      font-weight: 600;
      &:focus {
        outline: none;
      }
    }
  }
  .imageGroup {
    display: none;
    justify-content: center;
    align-items: center;
    .imgContainer {
      position: relative;
      .mainImg {
        border-top-left-radius: 30px;
        border-bottom-right-radius: 30px;
        position: relative;
        width: 350px;
        z-index: 2;
      }
      .minorImg {
        position: absolute;
        top: -70px;
        width: 140px;
        right: -70px;
        border-radius: 25px;
        z-index: 3;
      }
      .minorImg2 {
        position: absolute;
        bottom: -70px;
        width: 140px;
        left: -70px;
        border-radius: 25px;
        z-index: 3;
      }
      .firstCircle {
        position: absolute;
        top: 0px;
        width: 140px;
        right: -70px;
        z-index: 1;
      }
      .secondCircle {
        position: absolute;
        bottom: 0px;
        width: 140px;
        left: -70px;
      }
    }
  }
}

.featureSection {
  padding: 40px 30px;
  text-align: center;
  background-color: #f5f5f5;
  .container {
    margin: 0 auto;
    max-width: 1200px;
  }
  .featureBox {
    background-color: #ffffff;
    border-radius: 15px;
    box-shadow: 8px 15px 30px #0000001a;
    padding: 30px 15px;
    .featureIcon {
      font-size: 2.8rem;
    }
    h2 {
      margin-top: 20px;
      font-weight: 600;
      color: #212121;
      font-size: 1.45rem;
    }
    p {
      margin-bottom: 0;
      // width: 50%;
      margin: auto;
      font-size: 1.1rem;
      font-family: "Source Sans Pro", sans-serif;
      font-weight: 600;
    }
  }
}

.aboutSection {
  padding: 40px 30px;
  text-align: center;
  background-color: #f4e7f2;
  .container {
    margin: 0 auto;
    max-width: 1200px;
    h5 {
      font-size: 1rem;
      font-family: "Source Sans Pro", sans-serif;
      font-weight: 600;
      color: #a02089;
    }
    h2 {
      margin-top: 15px;
      font-weight: 600;
      color: #212121;
      font-size: 1.6rem;
      margin-bottom: 25px;
    }
    p {
      font-size: 1.1rem;
      font-family: "Source Sans Pro", sans-serif;
    }
    .founderQuote {
      font-size: 1.25rem;
      font-weight: 600;
      color: #002748;
    }
    .quoter {
      font-size: 1.2rem;
      span {
        font-weight: 600;
      }
    }

    button {
      font-size: 1.1rem;
      font-family: "Source Sans Pro", sans-serif;
      font-weight: 600;
      padding: 10px 32px;
      margin-top: 15px;
      border-radius: 12px;
      border: 2px solid #741763;
      background-color: rgba(0, 0, 0, 0);
      color: #741763;
      transition: 0.5s;
      &:hover {
        background-color: #741763;
        color: #fff;
      }
      &:focus {
        outline: none;
      }
    }
    .imageGroup {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      .imageContainer {
        position: relative;
        img {
          position: relative;
          width: 300px;
          z-index: 3;
        }
        .rect {
          background-color: #841fe8;
          width: 350px;
          height: 350px;
          border-radius: 35px;
          position: absolute;
          left: calc(50% - 175px);
          bottom: 0;
        }
      }
    }
  }
}

.productSection {
  padding: 40px 30px;
  background-color: #f5f5f5;
  .container {
    margin: 0 auto;
    max-width: 1200px;
    h5 {
      text-align: center;
      font-size: 1rem;
      font-family: "Source Sans Pro", sans-serif;
      font-weight: 600;
      color: #a02089;
    }
    h2 {
      text-align: center;
      font-weight: 600;
      color: #212121;
      font-size: 1.6rem;
      margin: 0 auto;
      margin-bottom: 80px;
    }
    .productBox {
      background-color: #ffffff;
      box-shadow: 15px 20px 35px #00000017;
      border-radius: 14px 14px 0px 0px;
      padding: 20px 30px;
      height: 100%;
      .iconWrapper {
        height: 60px;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
        .credit {
          width: 50px;
        }
        .notes {
          width: 30px;
        }
        .advisory {
          width: 50px;
        }
        .alternative {
          width: 50px;
        }
      }
      h3 {
        font-family: "Kanit", sans-serif;
        font-weight: 600;
        font-size: 1.5rem;
        margin-top: 30px;
        margin-bottom: 18px;
      }
      p {
        font-size: 1.1rem;
        font-family: "Source Sans Pro", sans-serif;
      }
    }
  }
}

.repeatSection {
  padding: 40px 30px;
  background-color: #f5f5f5;
  .container {
    margin: 0 auto;
    max-width: 1200px;
    .styledBox {
      background-color: #741763;
      border-radius: 25px;
      padding: 40px 20px;
      position: relative;
      .circlePatternOne {
        position: absolute;
        left: -200px;
        bottom: -100%;
        // display: none;
      }
      .circlePatternTwo {
        position: absolute;
        right: -200px;
        top: -100%;
        z-index: 1;
        display: none;
      }
      .circle {
        border-radius: 100%;
        position: absolute;
        display: none;
      }
      .firstCircle {
        width: 180px;
        height: 180px;
        background-color: #f5f5f534;
        top: -90px;
        left: -90px;
        display: none;
      }
      .secondCircle {
        width: 70px;
        height: 70px;
        background-color: #f5f5f534;
        top: 22.5px;
        left: 45px;
        display: none;
      }
      .thirdCircle {
        width: 180px;
        height: 180px;
        background-color: #f5f5f534;
        left: 29%;
        bottom: -100px;
        display: none;
      }
      .fourthCircle {
        width: 44px;
        height: 44px;
        background-color: #f7ceef9a;
        left: 32%;
        bottom: 56px;
        display: none;
      }
      .fifthCircle {
        width: 70px;
        height: 70px;
        background-color: #f49ee453;
        left: 43%;
        top: 22.5px;
        display: none;
      }
      .sixthCircle {
        width: 44px;
        height: 44px;
        background-color: #f7ceef9a;
        left: calc(43% + 50px);
        top: calc(70px - 22.5px);
        display: none;
      }
      .seventhCircle {
        width: 180px;
        height: 180px;
        background-color: #f5f5f534;
        right: 0;
        bottom: 35px;
        display: none;
      }
      .eigthCircle {
        width: 70px;
        height: 70px;
        background-color: #f49ee453;
        right: 20px;
        bottom: 15px;
        display: none;
      }
      .ninthCircle {
        width: 44px;
        height: 44px;
        background-color: #f7ceef9a;
        right: 0;
        bottom: 50px;
        display: none;
      }
      .access {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      h2 {
        font-size: 1.9rem;
        text-align: center;
        color: #fff;
        font-weight: 600;
        width: 90%;
        margin-bottom: 30px;
      }
      input {
        display: block;
        margin: 0 auto;
        background-color: #e2e2e2;
        border: 1px solid #e2e2e2;
        text-align: center;
        border-radius: 35px;
        padding: 10px 25px;
        width: 100%;
        font-family: "Source Sans Pro", sans-serif;
        font-size: 1.1rem;
        &:focus {
          outline: none;
        }
      }
      button {
        background-color: #f5f5f5;
        border: 1px solid #f5f5f5;
        z-index: 5;
        position: relative;
        border-radius: 35px;
        display: block;
        margin: 0 auto;
        margin-top: 20px;
        padding: 12px 25px;
        width: 100%;
        font-family: "Source Sans Pro", sans-serif;
        font-size: 1.1rem;
        font-weight: 600;
        color: #741763;
        &:focus {
          outline: none;
        }
      }
    }
  }
}

.contactSection {
  padding: 40px 30px 30px;
  background-color: #f5f5f5;
  .container {
    max-width: 900px;
    margin: 0 auto;
