        &:hover {
          background-color: #741763;
          color: #fff;
        }
        &:focus {
          outline: none;
        }
      }
    }
  }
}

.subFooter {
  padding: 80px 100px;
  background-color: #a02089;
  margin-top: -250px;
  position: relative;
  z-index: 2;
